import Head from 'next/head';
import { withRouter } from 'next/router';
import fetch from 'isomorphic-unfetch';

import PlayerLayout from '../components/layout';

const Content = withRouter(props => (
    <div>
        <h1>{props.router && props.router.query && props.router.query.episodeId}</h1>
        <ul>
            {props.shows.map(show => (
                <li key={show.id}>
                    <Link as={`/p/${show.id}`} href={`/post?id=${show.id}`}>
                        <a>{show.name}</a>
                    </Link>
                </li>
            ))}
        </ul>
    </div>
));

const EmbedPlayer = props => (
    <div className="embed-player">
        <Head>
            <title>Embedded Player</title>
            <meta property="og:description"
                  content='testing!'/>
        </Head>
        <Content {...props}/>
    </div>
);

EmbedPlayer.getInitialProps = async function() {
    const res = await fetch('https://api.tvmaze.com/search/shows?q=batman');
    const data = await res.json();

    console.log(`Show data fetched. Count: ${data.length}`);

    return {
        shows: data.map(entry => entry.show)
    };
};

export default PlayerLayout(EmbedPlayer);